import React from 'react';
import 'tachyons';
import Slider from 'react-slick';
import './AboutUs.css';
import { APP_NAME } from '../../constants/otherConstants';
import logo from '../../assets/Images/complete_logo.png';
import brain from '../../assets/Images/brain.png';
// import TextInput from '../../common/components/TextInput';

function importAll(r) {
	return r.keys().map(r);
}
  
const ourServices = importAll(require.context('../../assets/Images/our_services/', false, /\.(png|jpe?g|svg)$/));
const feedbacks = importAll(require.context('../../assets/Images/feedbacks/', false, /\.(png|jpe?g|svg)$/));


function AboutUs() {
	
	const sendForm = (e) =>{
		e.preventDefault();
	};
	const settings = {
		dots: true,
		infinite: true,
		speed: 500,
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: true,
		autoplaySpeed: 2000,
	};
	const ourClientSettings = {
		dots: true,
		infinite: true,
		speed: 500,
		slidesToShow: 1,
		autoplay: true,
		autoplaySpeed: 3000,
	};
      
	return (
		<div className='about-us'>
			<div className="about-us-header" >
				<div className="tc">
					{/* <h4>{APP_NAME}</h4> */}
					<img src={logo}/>
	 				<h2>OUR ONGOING SERVICES</h2>
					{/*				<p>We are all very different. We were born in different cities, at different times, we love different music, food, movies. But we have something that unites us all. It is our company. We are its heart. We are not just a team, we are a family.</p>
					<button type='button'>
						<a href='/#contact-us' >Contact US</a>
					</button> */}
                    
				</div>
			</div>
			{/* <div className='about-us-middle-img'/> */}
			<div className='our-team-carousel'>
				<Slider {...settings}>
					{
						ourServices.map((t,i) => (
							<div className='item' >
								<img src={t} />
								
							</div>
						))    
					}
				</Slider>
			</div>
			<div className='about-us-third-content'>
				<div className='content'>
					<h1>Who are we?</h1>
					<p>
			We, The Soulful Mind are an organisation that works towards promoting mental health awareness and breaking social stigma.
						<br/>
				Over 200 students from all over India have partaken in our programs.
						<br/>
				At The Soulful Mind, our key focus is to educate the society about mental health issues and normalise the stigma associated with mental illnesses. 
						<br/>
				We conduct online and offline workshops and host internships revolving around mental health issues.
					</p>
				</div>
				
				<img src={brain}/>
			</div>
			<div className='about-us-our-clients'>
				<h2>Client feedbacks</h2>
				<h3>Thank you for your feedbacks it help us improving!</h3>
				<div className='carousel'>
					<Slider {...ourClientSettings}>
						{
							feedbacks.map((t,i) => (
								<div className='item' >
									<img src={t} />
								
									
								
								</div>
							))    
						}
					</Slider>
				</div>
			</div>
			{/* <div className='about-us-contact-us' id='contact-us'>
				<h2>CONTACT US</h2>
				<form onSubmit={sendForm}>
					<div className='flex-input'>
						<div className='flex flex-column'>
						    <label>Name</label><input />
						</div>
						<div className='flex flex-column'>
						    <label>Email</label><input />
						</div>
						<div className='flex flex-column'>
						    <label>Phone</label><input />
						</div>
					</div>
					
					<div className='flex flex-column'>
						<label>Message</label><textarea  />
					</div>
					
					<button type='submit'>
                        Send form
					</button>
				</form>
			</div> */}
		</div>
	);
}

export default AboutUs;
